.team{
    width: 100vw;
    text-align: center;
    margin-top: 4rem;

}
.team-main{
    padding-top: 3rem;
    padding-bottom: 2rem;
    border-top: 1px solid #3b3b3b;
    border-bottom: 1px solid #3b3b3b;
    width: 1200px;
    max-width: 90vw;
    margin: auto;
}
.team-inner{

    
    width: 1200px;
    max-width: 90vw;
    margin: auto;
    display: flex;
    justify-content: space-between;
 
    // background-color: red;
    text-align: start;
    margin-top: 3rem;
    flex-wrap: wrap;


}
.team-wrapper{
    width: 270px;
    max-width: 90vw;

    display: flex;
    flex-direction: column;
    align-items: center;
    margin:0 0 2rem 0;
    img{
        width: 100%;
        border-radius: 25px;
    }
    span{
        margin: 0.5rem 0 0.3rem 0;
       
    }
    p{
        text-align: center !important;
    
    }
}

#team-clickable{
    cursor: url(../../assets/images/cursor/pointer.png) ,auto !important;
}

@media screen and (max-width:500px){
    .team-inner{
        flex-direction: column;
        align-items: center;
    }
    .team-wrapper{
      margin-top: 3rem;
      text-align: center;
    }
}
@media screen and (max-width:850px){
    .team-inner{
        flex-direction: column;
        align-items: center;
        margin-top: -1rem;
    }
    .team-wrapper{
      margin:unset;
      margin-top: 4rem;
      text-align: center;
    }
}