.footer{
    width: 100vw;
    // height: 150px;
    // box-shadow: 0px 0px 5px #62ebfa;
    margin: 5rem 0 0 0;
    padding: 1rem 0 1rem 0;
    // background-color: #2d2b2b;
}
.footer-inner{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    span{
        margin: 1rem 0 0.6rem 0;
    }
    padding-bottom: 2rem;
}
.footer-logo{
    width: 180px;
}
.footer-social-wrapper{
    display: flex;
    margin-bottom: 0.5rem;
}
.footer-social-btn{
    display: flex;
    align-items: center;
    justify-content: center;
    height:25px;
    width: 25px;
    margin: 0.6rem;
    img{
        height: 100%;
    }
}