.top-section{
    width: 100vw;
    margin-top:20px;
}
.top-section-inner{
    width: 1200px;
    max-width: 90vw;
    margin: auto;
}
.top-section-wrapper{
    width:1200px;
    max-width: 90vw;
    height: calc(1200px * 0.5625);
    max-height: calc(90vw * 0.5625);
    background-image: url(../../assets/images/banner.webp);
    background-size: contain;
}
@media screen and (max-width:600px){
    .top-section-inner{
        max-width: 100vw;
    }
    .top-section-wrapper{
     
        max-width: 100vw;
        max-height: calc(100vw * 0.5625);
    }
}
