.charity{
    width: 100vw;
    margin-top: 4rem;
    text-align: center;
}
.charity-inner{
    display: flex;
    justify-content: space-between;
    width: 1200px;
    max-width: 90vw;
    margin: auto;
    margin-top: 4rem;
    margin-bottom: 4rem;
    p{
        text-align: justify;
       
       
    }

}
.charity-left{
    img{
        width: 300px;
        margin-right: 4rem;
    }
}

.charity-list{
    width: 800px;
    max-width: 90vw;
    flex-wrap: wrap;
    margin: auto;
    display: flex;
    align-items: center;
    justify-content: space-between;

    margin-top: 2rem;
    img{
        height: 100px;
    }

}
.charity-list-2{
    height: 60px !important;
}
@media screen and (max-width:900px) {
   
    .charity-inner{
        flex-direction: column;
        width: 500px;
        margin-top: 1rem;
    }
    .charity-left{
        margin-bottom: 1rem;
        img{
            width: 300px;
            margin-right: 0;
        }
    }

    .charity-left{
        img{
            width: 300px;
            margin-right: 4rem;
        }
    }
}
@media screen and (max-width:800px) {
   
    .charity-list{
      
        img{
            margin: 1.5rem;
        }
    }
}
@media screen and (max-width:500px) {
    .charity{

        margin-top: 5rem;
    }
    .charity-inner{
        p{
            text-align: justify;
        }
    }
    .charity-list{
        flex-direction: column;
    }
}