.roadmap{
    width: 100vw;
    margin-top: 3rem;
    margin-bottom: 4rem;
}
.roadmap-inner{
    padding-top: 3rem;

    border-top: 1px solid #3b3b3b;
    border-bottom: 1px solid #3b3b3b;
    width: 1200px;
    max-width: 90vw;
    margin: auto;
}
.roadmap-header{
    // background-color: red;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    img{
        height: 270px;
    }
}
.roadmap-title-img{
    margin-bottom: -100px;
    width: 100%;
}

.roadmap-body{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    // background-color: brown;
    margin: 8rem auto 0 auto;
    width: 870px;
}
.roadmap-section{
    position: relative;
    display: flex;
    
    margin: 0 auto 7.5rem auto;
    width: fit-content;
    // background-color: red;
    width: 100%;
    text-align: justify;
    
    // height: fit-content;
}
#roadmap-section-2,#roadmap-section-4,#roadmap-section-6{
    display: flex;
    flex-direction: row-reverse;
}
#roadmap-section-6{
    margin: 0 auto 3rem auto;
}
#roadmap-section-1-img{
    position: relative;
    height: 181px;
    margin: 0 0.3rem 0 0;
    top:-5px
}
#roadmap-section-2-img{
    width: 200px;
    position: relative;
    top:-20px;
    margin: 0 0 0 1rem ;
}
#roadmap-section-3-img{

    position: relative;
    margin: 0px  1.5rem  0 0 ;
    width: 220px;
}
#roadmap-section-4-img{
    position: relative;
    margin: 0px 0 0 1.5rem;
    width: 175px;
    top: -10px;
}
#roadmap-section-5-img{
    margin: 90px 1.5rem 0 0;
    width: 225px;
}
#roadmap-section-6-img{
    position: relative;
    margin: 0px 0 0 1.5rem;
    width: 120px;
    top: -14px;
}
.roadmap-section-left{
    display: flex;
    flex-direction: column;
    // margin-left: 3.5rem;
    // left: 240px;
    // margin-left: 1.5rem;
    // background-color: blue;
}

.roadmap-section-title{
    display: flex;
    flex-direction: column;
    margin: 0 0 30px 0;
}
.roadmap-mobile-img{
    display: none;
    width: 90vw;
    max-width: 470px;
    margin-bottom: 20px;
}
#roadmap--section-6-img-mobile{
    width:unset;
    // height: 473px;
    height: 303px;
    margin-left: auto;
    margin-right: auto;
}
// #roadmap--section-1-img-mobile{
//     width:unset;
//     height: 200px;
//     margin-left: auto;
//     margin-right: auto;
//     width: 203px;
// }
// #roadmap--section-4-img-mobile{
//     width: 230px;
//     margin-left: auto;
//     margin-right: auto;
// }
@media screen and (max-width:961px){
    #phase-1-subtitle{
        width: 250px;
        margin: auto;
    }
    .roadmap-body{
        width: 90vw;
        max-width: 470px;
        margin: 4rem auto 0 auto;
    }
    .roadmap-section{
        margin: 0 0 4.5rem 0;
        height: fit-content !important;
        flex-direction: column;
    }
    .roadmap-section-right{
        position: relative;
        left: unset;
        margin-left: unset;
    }
    .roadmap-section-left{
        display: none;
    }
    .roadmap-mobile-img{
        display: block;
    }
    .roadmap-section-title{
        text-align: center;
    }
    .roadmap-title-img{
        display: none;
    }
    .roadmap-title{
        display: block;
    }
    .roadmap-header{
        img{
            margin-top: 1rem;
        }
    }
}
@media screen and (max-width:580px){
    .roadmap-header{
        img{
            height: 165px;
        }
    }
    .roadmap-title-img{
        margin-bottom: -20px;
        width: 113%;
    }
}

