.sanctuary{
    width: 100vw;
    margin-top: 5rem;
    text-align: center;
 
}
.sanctuary-main{
    padding-top: 5rem;
    padding-bottom: 3rem;
    border-top: 1px solid #3b3b3b;
    border-bottom: 1px solid #3b3b3b;
    width: 1200px;
    max-width: 90vw;
    margin: auto;
}
.sanctuary-inner{
  

    display: flex;
    justify-content: space-between;
    width: 1200px;
    max-width: 90vw;
    margin: auto;
    margin-top: 4rem;
    p{
        text-align: justify;
       
       
    }
}
.sanctuary-right{
    img{
        position: relative;
        max-width: 90vw;
        width: 400px;
        margin-left: 4rem;
        top:-70px
    }
}
@media screen and (max-width:900px) {
   
    .sanctuary-inner{
        flex-direction: column-reverse;
        width: 500px;
        margin-top: 1rem;
    }
    .sanctuary-right{
        margin-bottom: 1rem;
        img{
         
            margin-left: 0;
        }
    }
}
@media screen and (max-width:500px) {
    .sanctuary{

        margin-top: 5rem;
    }
    .sanctuary-inner{
        p{
            text-align: justify;
        }
    }
}