.carousel{
    position: relative;

    box-sizing: content-box;

    margin-top: 0rem;
}

.carousel-roadmap-bg{
    height: 100%;
    width: 100%;
    position: absolute;
    z-index: 1;
    top:0;
}

.carousel-inner{
    position: relative;
    z-index: 2;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
    width: 100%;
}
.carousel-wrapper-1{
    position: relative;
    overflow: hidden;
    width:100%;
    height: 300px;
}

.carousel-track-1,.carousel-track-2{
    position: absolute;
    display: flex;
    height: 100%;
    width: fit-content;
}
// .carousel-track-1{
//     // left:0;
// }
// .carousel-track-2{
//     right:0;
// }
.carousel-img{
    height: 300px;
    margin-right: 1.05rem;
}
@media screen and (max-width:700px) {

    .carousel-wrapper-1{
      
        height: 220px;
    }
    .carousel-img{
        height: 220px;
    }
}
