$gold:#c1b159 ;

.header{
    position: sticky;
    top:0;
    width: 100vw;
    backdrop-filter: blur(5px);
    z-index: 1000;
    background-color: rgba(0,0,0,0.7);

}
.header-inner{

    display: flex;
    justify-content: space-between;
    width: 1200px;
    max-width: 90vw;
    height: 120px;
    padding-top: 10px;
    padding-bottom: 10px;
    box-sizing: border-box;
    margin: auto;
}
.header-left{
    display: flex;
    height: 100%;
}
.header-links{
    display: flex;
    align-items: center;
}
.header-right{
    display: flex;
    align-items: center;
    height: 100%;
}
.header-logo{
    height: 100%;
}
.header-link{
    margin: 0 0 0 30px;
    &:hover{
        color:$gold;
    }
}
.header-social{
    margin: 4px 0 0 50px;
    display: flex;
    align-items: center;
}
.discord-icn{
    width: 20px;
    height: 20px;
    margin: 0 10px 0 ;
}
.twitter-icn{
    width: 20px;
    height: 20px;
    margin: 0 10px 0 ;
}
.medium-icn{
    width: 35px;
}
.opensea-icn{
    width: 20px;
    margin: 0 10px 0 ;
}

.header-connect{
    width: 145px;
    height: 34px;
    // border-radius: 6px;
    border: 1px solid white;
    overflow: hidden;
    margin: 0 0px 0 20px;

    &:hover{
        background-color: $gold;
        border: 1px solid transparent;
    }
}


.header-hamburger{
    margin: 0 0px 0 20px;
    z-index: 20;
    display: none;
    justify-content: center;
    align-items: center;
    height: 40px;
    width: 40px;
    cursor: pointer;
}
.header-hamburger-inner{
    display: flex;
    justify-content: center;
    position: relative;
    height: 40px;
    width: 40px;
}

.hamburger-top,.hamburger-bottom{
    height: 2px ;
    width: 60%;
    background-color: white;
    border-radius: 1px ;
}
.hamburger-top{
    position: absolute;
    top:14px;
}
.hamburger-bottom{
    position: absolute;
    bottom:14px;
}

.header-mobile-nav{

    display: none;
    z-index: 15;
    top:0;
    left:0;
    position: fixed;
    height: 100vh;
    width: 100vw;
    background-color: rgba(0, 0, 0, 0.518);
    backdrop-filter:blur(25px) !important;
    overflow: scroll;
    opacity: 0;
    transition:all 350ms ease-in-out;
}
.header-mobile-nav-inner{
    margin:20px auto 0 auto;
    display: flex;
    flex-direction: column;
    // width: 600px;
    width: 86vw;
    // background-color: green;
}
.header-mobile-logo{
    margin-bottom: 1rem;
    height: 100px;
}
.mobile-nav-button{
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width:100%;
    height: 39px;
    border-bottom: 1px solid #7A7570;
    img{
        height: 18px;
    }
}


@media screen and (max-width:1234px){

    .header{
        backdrop-filter: unset;
    }
    .header-links{
        display: none;
    }
    .header-social{
        display: none;
    }
    .header-hamburger{
        display: flex;
    }
    .header-mobile-logo{
        display: block;
      
    }
}

@media screen and (max-width:750px){
    .header-inner{
        height: 70px;
        padding-top: 5px;
        padding-bottom:5px;
    }
}