$white:#ffffff;
$green:#84834e;

.MintSection{
   width: 100vw;
   margin-top: 3rem;
 

}
.mint-section-main{
    padding-top: 3rem;
    border-top: 1px solid #3b3b3b;
    width: 1200px;
    max-width: 90vw;
    margin: auto;
}
.MintSection-inner{

    display: flex;
    position: relative;
    justify-content: space-between;
    align-items: center;
    background-color: #000;
    width:850px;
    // height: 468px;
    margin: auto;
    box-sizing: border-box;
    transition: all 200ms ease-in-out;
}

.MintSection-left{
    display: grid;
    flex-direction: column;
    text-align: center;
}

.Mint-section-right>video{
    height: 375px;
    width: 375px;
    border-radius: 25px;
}


@media  screen and (max-width:930px) {
    .MintSection-inner{
        flex-direction: column-reverse;
        height: fit-content;
        width: 90vw;
    }
    .MintSection-left{
        text-align: center;
    }
    .Mint-section-right>video{
        margin-bottom: 3rem;
        height: 90vw;
        width:90vw;
        max-width: 400px;
        max-height: 400px;
    }
}

.MintSection-left-bottom{
    display: flex;
    margin-top: 20px;
}

.MintSection-amount-top,.MintSection-amount-bottom{
    margin:0 0 20px 0;
    display: flex;
  
    height: 55px;
    width:300px;
    justify-content: space-between;
}
.MintSection-amount-bottom{
    margin:0;
    width: 300px;
}
.MintSection-amount-top>input{
    height: 51px;
    border:2px solid $green;
    outline:none;
    background: transparent;
    max-width: 55%;
    text-align: center;
}
.mint-plus-btn,.mint-button,.mint-amount-btn{
    height: 100%;
    border:2px solid $green;
    cursor: pointer;
    background: transparent;
}
.mint-plus-btn{
    width:55px;
}

.mint-plus-btn:hover{
    background:  $green;
    color:black;
}

.mint-button{
    color: black;
    height: 55px;
    width:300px;
    background:  $green;
}
.mint-button:hover{
    background: transparent;
    color:$white;
}
.mint-amount-btn{
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    width:30%;
    height:85%;
}
.mint-amount-btn:hover{
    background: $green;
    color:black;
}
.mint-amount-btn>img{
    height:78%;
}
.Mintsection-right-img{
    align-items: flex-start;
 
    height:350px;
    width:350px;
    overflow: hidden;
}
.Mintsection-right-img>img{
    width:100%;

}

@media  screen and (max-width:500px) {
    .MintSection{
        margin-top: 3rem;
    }
    .Mint-section-right>video{
        margin-bottom: 1.6rem;
    }
}