
$green:#84834e;

.faq-section{
    margin-top: 5rem;
    width: 100vw;
    // transition: all 400ms ease-in;
}
.faq-section-inner{
    margin:auto;
    width: 1200px;
    max-width: 90vw;
}
.faq-title{
    text-align: center;
}
.faq{
    width: 100%;
    display: flex;
    flex-direction: column;
}
.faq-question{
    height: 70px;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    padding: 0 15px 0 10px;
    box-sizing: border-box;
    border-bottom:2px solid $green;
   
    transition: all 200ms ease-in;
    font-family: 'KOMIKAHB',sans-serif;
    font-size:1rem !important;


    cursor: url(../../assets/images/cursor/pointer.png) ,auto !important;

}
@media screen  and (max-width:725px) {
    .faq-section{
        margin-top: 4rem;
        width: 100vw;
        transition: all 400ms ease-in;
    }

    .faq-question-title{
        max-width: 95%;
    }
}

@media screen  and (max-width:425px) {
    .faq-question-title{
        font-size:1rem !important;
    }
}


.faq-question>img{
    height: 17px;
    transition: transform 200ms ease-in;
}
.faq-answer{
    height: 0px;
    overflow: hidden;
    transition: height 200ms ease-in;
    a{
        color:$green
    }
    text-align: justify;
}
.faq-answer-inner{
    padding: 20px ;
    height: fit-content;
}